import { Form, Formik } from "formik";
import React from "react";
import FormikInputFieldComponent from "../../shared-components/inputs/FormikInputField.component";
import FormikSelectFieldComponent from "../../shared-components/inputs/FormikSelectField.component";
import { ProductFormType } from "../models/product.type";
import ButtonComponent from "../../shared-components/buttons/Button.component";
import useProductStore from "../../store/hooks/use-product-store.hook";
import kababCaseToTitleCase from "../../helpers/kebab-case-to-title-case.hepler";
import * as yup from "yup";

type AddProductProps = {
  onSubmit: (values: ProductFormType) => void;
};

const AddProduct = ({ onSubmit }: AddProductProps) => {
  const { productCategories, setProductCategories } = useProductStore();
  const categories = React.useMemo(() => {
    return productCategories.map((category) => ({
      value: category,
      label: kababCaseToTitleCase(category),
    }));
  }, [productCategories]);

  React.useEffect(() => {
    if (productCategories.length === 0) {
      setProductCategories();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [productCategories]);

  const validationSchema = yup.object().shape({
    brand: yup.string().required("Brand is required"),
    category: yup.string().required("Category is required"),
    description: yup.string().required("Description is required"),
    discountPercentage: yup
      .number()
      .min(0, "Discount percentage must be greater than or equal to 0")
      .max(100, "Discount percentage must be less than or equal to 100")
      .required("Discount percentage is required"),
    price: yup.number().min(0, "Price must be greater than or equal to 0").required("Price is required"),
    rating: yup
      .number()
      .min(0, "Rating must be greater than or equal to 0")
      .max(5, "Rating must be less than or equal to 5")
      .required("Rating is required"),
    stock: yup.number().min(0, "Stock must be greater than or equal to 0").required("Stock is required"),
    title: yup.string().required("Title is required"),
  });

  const initialValues: ProductFormType = {
    brand: "",
    category: "",
    description: "",
    discountPercentage: 0,
    price: 0,
    rating: 0,
    stock: 0,
    title: "",
  };
  return (
    <div className="p-5 h-full overflow-auto">
      <Formik initialValues={initialValues} validationSchema={validationSchema} onSubmit={onSubmit}>
        <Form className="grid grid-cols-2 gap-5">
          <FormikInputFieldComponent fieldName="brand" />
          <FormikSelectFieldComponent fieldName="category" options={categories} />
          <FormikInputFieldComponent fieldName="description" />
          <FormikInputFieldComponent fieldName="discountPercentage" type="number" />
          <FormikInputFieldComponent fieldName="price" type="number" />
          <FormikInputFieldComponent fieldName="rating" type="number" />
          <FormikInputFieldComponent fieldName="stock" />
          <FormikInputFieldComponent fieldName="title" />
          <div className="col-span-2 flex justify-end">
            <ButtonComponent type="submit" text="Add"></ButtonComponent>
          </div>
        </Form>
      </Formik>
    </div>
  );
};

export default React.memo(AddProduct);
