import AppStore from "..";
import ProductStore from "../slices/product-store.slice";

const useProductStore = () => {
  const dispatch = AppStore.useAppDispatch();
  const selectProductList = AppStore.useAppSelector(ProductStore.select.getProductList);
  const selectProductCategories = AppStore.useAppSelector(ProductStore.select.getProductCategories);
  const dispatchSearchProductList = (searchKeyword: string) => {
    dispatch(ProductStore.searchProductList(searchKeyword));
  };

  const dispatchSetProductCategories = () => {
    dispatch(ProductStore.getProductCategories());
  };

  const dispatchResetProductStore = () => {
    dispatch(ProductStore.actions.reset());
  };

  return {
    productList: selectProductList,
    productCategories: selectProductCategories,
    searchProductList: dispatchSearchProductList,
    resetProductStore: dispatchResetProductStore,
    setProductCategories: dispatchSetProductCategories,
  };
};

export default useProductStore;
