import React from "react";
import SectionHeadingComponent from "../shared-components/SectionHeading.component";
import SearchInputComponent from "../shared-components/inputs/SearchInput.component";
import useProductStore from "../store/hooks/use-product-store.hook";
import ProductListComponent from "../products/components/ProductList.component";
import ButtonComponent from "../shared-components/buttons/Button.component";
import useToastrStore from "../store/hooks/use-toastr-store.hook";
import AddProductComponent from "../products/components/AddProduct.component";
import DialogHeaderComponent from "../shared-components/dialog/DialogHeader.component";

const ProductPage = () => {
  const dialogRef = React.useRef<HTMLDialogElement>(null);
  const { addToastr } = useToastrStore();
  const [searchKeyword, setSearchKeyword] = React.useState<string>("");
  const { searchProductList, resetProductStore } = useProductStore();

  const handleOnSearch = React.useCallback(
    (value: string) => {
      setSearchKeyword(value);
      if (!value) {
        addToastr({
          message: "Please enter search keyword",
          variant: "danger",
        });
        return;
      }
      searchProductList(value);
    },
    [searchProductList, addToastr]
  );

  const handleCloseDialog = React.useCallback(() => {
    dialogRef.current?.close();
  }, [dialogRef]);

  const handleAddProduct = () => {
    dialogRef.current?.showModal();
  };

  const handleSubmitForm = React.useCallback(() => {
    addToastr({
      message: "Product added successfully",
      variant: "success",
    });
    handleCloseDialog();
  }, [addToastr, handleCloseDialog]);

  React.useEffect(() => {
    return () => resetProductStore();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <>
      <div aria-roledescription="product" className="h-full">
        <SectionHeadingComponent title="Product" subtitle="View, edit, add, or delete product" />
        <div className="p-5 h-[calc(100%-64px)]">
          <div className="flex justify-between pb-5">
            <SearchInputComponent placeholder="Search products..." onSearch={handleOnSearch} />
            <div>
              <ButtonComponent text="Add Product" onClick={handleAddProduct} />
            </div>
          </div>
          <div className="h-[calc(100%-56px)] overflow-auto">
            <ProductListComponent keyword={searchKeyword} />
          </div>
        </div>
      </div>
      <dialog
        aria-roledescription="add product dialog"
        ref={dialogRef}
        className="rounded w-1/3 shadow-2xl ring-1 ring-gray-300 h-2/4"
      >
        <div className="h-full flex flex-col">
          <DialogHeaderComponent title="Add Product" onClose={handleCloseDialog} />
          <AddProductComponent onSubmit={handleSubmitForm} />
        </div>
      </dialog>
    </>
  );
};

export default React.memo(ProductPage);
