import React from "react";
import ButtonIconComponent from "../buttons/ButtonIcon.component";
import IconCloseComponent from "../icons/IconClose.component";

type DialogHeaderProps = {
  onClose: () => void;
  title: string;
};

const DialogHeader = ({ onClose, title }: DialogHeaderProps) => {
  return (
    <header className="flex items-center justify-between p-3 border-b">
      <span className="font-medium text-base">{title}</span>
      <ButtonIconComponent variant="danger" onClick={onClose}>
        <IconCloseComponent className="w-3 h-3" />
      </ButtonIconComponent>
    </header>
  );
};

export default React.memo(DialogHeader);
